<template>
<!--banner-->
  <div>
  <el-row class="softDetail">
    <el-col :span="3" :offset="4" class="detail_img">
      <img :src="softImg" height="100" width="100">
    </el-col>
    <el-col :span="8">
      <el-row><h1 class="soft_title">{{softTitle}}</h1></el-row>
      <el-row><span class="soft_introduce">
        {{softDescription}}
      </span>
      </el-row>
      <el-row>
        <el-col :span="10" class="detail_score" >
          <el-col :span="8"><span></span></el-col>
          <el-col :span="16"><el-rate
            v-model="detailScore"
            disabled
            text-color="#ff9900"
            score-template="{value}">
          </el-rate></el-col>
        </el-col>
        <el-col :span="6" :offset="4">
          <el-button v-if="!ifCollect" type="text" @click="collectSoft(softId,1)" style="color: #ffffff"><i class="fa fa-heart-o"></i></el-button>
          <el-button v-if="ifCollect" type="text" @click="collectSoft(softId,0)" style="color: #ffffff"><i class="fa fa-heart"></i></el-button>
        </el-col>
      </el-row>
      <el-row>
        <el-button v-if="ifGet==='false'||ifGet===undefined" type="button" size="medium" class="detail_down" @click="downSoft()"></el-button>
        <el-button v-if="ifGet==='true'" type="button" size="medium" class="detail_down" disabled></el-button>
      </el-row>
  </el-col>
    <el-col :span="4" :offset="1" class="detail_info">
      <el-card class="box-card">
        <el-row><span>{{creator}}</span></el-row>
        <el-row><span>{{createTime}}</span></el-row>
        <el-row><span>20</span></el-row>
        <el-row><span>{{creator}}</span></el-row>
        <el-row><span>13233362654</span></el-row>
      </el-card>
    </el-col>
  </el-row>
  <el-row class="detail_banner">
    <el-col :span="2" :offset="8">
      <el-link href="#info"></el-link>
    </el-col>
    <el-col :span="2">
      <el-link href="#specialFunction"></el-link>
    </el-col>
    <el-col :span="2">
      <el-link href="#support"></el-link>
    </el-col>
    <el-col :span="2" >
      <el-link href="#price"></el-link>
    </el-col>
  </el-row>
    <el-row class="soft_info">
      <el-col :span="15" :offset="5">
        <el-row id="info">
          <el-row>
            <el-divider direction="vertical" class="direction"></el-divider>
            
          </el-row>
          <el-row>
            <el-col :span="18" :offset="2" class="introduce">
              <span>
                 &nbsp;&nbsp;&nbsp;&nbsp;CreoPTC201010CADCreoPTCPro/EngineerCoCreateProductViewCADPTC
              </span>
            </el-col><el-col :span="18" :offset="2">
            <img src="../../assets/carousel/carousel_1.jpg" width="100%">
          </el-col></el-row>
          </el-row>
        <el-row id="specialFunction">
          <el-row>
            <el-divider direction="vertical" class="direction"></el-divider>
            
          </el-row>
          <el-row>
            <el-col :span="18" :offset="2" class="introduce">
              <span class="introduce">
                 &nbsp;&nbsp;&nbsp;&nbsp;PTCCreo
                CREOCAD
              </span><br>
              <span>  CAD </span><br>
              <span>  PTC </span><br>
              <span> </span><br>
              <span>  [1] </span><br>
            </el-col>
            <el-row>
            <el-col :span="18" :offset="2">
            <img src="../../assets/carousel/carousel_1.jpg" width="100%">
            </el-col>
            </el-row>
          </el-row>
        </el-row>
        <el-row id="support">
          <el-row>
            <el-divider direction="vertical" class="direction"></el-divider>
            
          </el-row>
          <el-row><el-col :span="15" :offset="3" style="margin-top: 20px"><span>  0532-123456</span></el-col></el-row>
          <el-row><el-col :span="15" :offset="3" style="margin-bottom: 20px"><span> support@iscas.com</span></el-col></el-row>
        </el-row>
        <el-row id="price">
          <el-row>
            <el-divider direction="vertical" class="direction"></el-divider>
            
          </el-row>
          <el-row>
            <el-col :span="15" :offset="3" class="introduce">
              <table>
                <tr><td></td><td></td><td></td></tr>
                <tr><td>50//</td><td>5000//</td><td>50000//</td></tr>
              </table>
            </el-col>
          </el-row>
        </el-row>
          <el-row>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
    export default {
        name: "softDetail",
      data(){
          return {
            detailScore:3.5,
            softId:"",
            softImg:"",
            softTitle:"",
            softDescription:"",
            creator:"",
            createTime:"",
            ifGet:"",
            ifCollect:false,
            homeDetail:null
          }
      },
      props: {
        detail:{
          type:Object,
          default(){return {}}
        }
      },
      created(){
        this.homeDetail=JSON.parse(sessionStorage.getItem('homeDetail'));
      },
      mounted(){
        this.pullInfo();this.homeToDetail();
      },
      methods:{
          pullInfo(){
            if(this.detail!=={}){
              this.softId=this.detail.id;
              this.softImg="data:image/jpg;base64,"+this.detail.softIcon;
              this.softTitle=this.detail.softName;
              this.softDescription=this.detail.description;
              this.createTime=this.detail.created;
              this.creator=this.detail.creator;
              this.ifGet=this.detail.auth;
              this.ifCollect=this.detail.collect;
            }
          },
        homeToDetail(){
          if(this.homeDetail!==null){
            this.softId=this.homeDetail.id;
            this.softImg="data:image/jpg;base64,"+this.homeDetail.softIcon;
            this.softTitle=this.homeDetail.softName;
            this.softDescription=this.homeDetail.description;
            this.createTime=this.homeDetail.created;
            this.creator=this.homeDetail.creator;
            this.ifGet=this.homeDetail.auth;
            this.ifCollect=this.homeDetail.collect;
            //session
            sessionStorage.removeItem('homeDetail');
          }
        },
        //
        downSoft(){
            if(sessionStorage.getItem("username") === null){
              this.$message({
                message: ",",
                type: 'warning'
              });
            }else{
              let param={
                username:sessionStorage.getItem('username'),
                softId:this.detail.id
              };
              this.$axios.get('/soft-auth/soft-order',{params:param}).then((res)=>{
                this.$message({
                  message: '',
                  type: 'success'
                });
                this.ifGet="true";
              }).catch((err)=>{
                console.log(err);
              });
            }
        },
        ///
        collectSoft(softId,ifCollect){
          if(sessionStorage.getItem("username") === null){
            this.$message({
              message: ",",
              type: 'warning'
            });
          }else {
            let collectMessage = "";
            if (ifCollect === 0) collectMessage = "";
            else collectMessage = "";
            let param = {
              username: sessionStorage.getItem('username'),
              softId: softId,
              collect: ifCollect
            };
            this.$axios.post('/soft-user/collectSoft', param).then((res) => {
              this.$message({
                message: collectMessage,
                type: 'success'
              });
              if(ifCollect===0)this.ifCollect = false;
              else this.ifCollect = true;
            }).catch((err) => {
              console.log(err);
            });
          }
        },
      }
    }
</script>

<style scoped>
.softDetail{
  min-height: 170px;
  padding: 40px;
  color: #ffffff;
  background: url("../../assets/soft/softDetail_bg.jpg");
}
  .soft_title{margin: 10px 0px;font-weight: normal}
  .detail_img{margin-top: 30px;}
  .detail_info span{color: #ffffff;}
  .detail_info .el-row{margin: 10px 0px}
  .detail_down{background: #fff0;color: #ffffff}
  .detail_banner{
    height: 40px;
    background: #f5f5f5;
    padding: 8px;}
  .soft_info .el-divider{
    background-color: #1875F0;
    width: 5px;
    height: 30px;
  }
.introduce{
  margin-top: 20px;
  margin-bottom: 20px;
}
  #info{margin-top: 20px}
#price table,#price table tr th, #price table tr td {
  border:1px solid #dddddd;
  padding: 5px 10px;
}
#price table {
  width: 100%;
  min-height: 25px;
  line-height: 25px;
  text-align: center;
  border-collapse: collapse;
}
</style>



